import { useState, useEffect } from 'react';
import { ethers } from 'ethers';

export const useContractOperations = (contract, account) => {
  const [userInfo, setUserInfo] = useState({
    referrer: '',
    claimsToday: 0,
    lastClaimDate: 0,
    lastClaimTime: 0,
    pendingEthRewards: '0',
    pendingTokenRewards: '0',
    totalEthEarned: '0',
    totalTokensEarned: '0',
    referralCount: 0
  });
  
  const [claimStatus, setClaimStatus] = useState({
    canClaim: true,
    message: '',
    timeLeft: 0
  });
  const [loading, setLoading] = useState({
    claimTokens: false,
    claimEthRewards: false,
    claimTokenRewards: false,
    bindReferrer: false,
    fetchingData: false
  });

  // 
  const fetchUserInfo = async (retryCount = 0) => {
    if (!contract || !account) return;
    
    try {
      setLoading(prev => ({ ...prev, fetchingData: true }));
      
      const [userInfoResult, claimStatusResult] = await Promise.all([
        contract.getUserInfo(account),
        contract.canClaim(account)
      ]);
      
      setUserInfo({
        referrer: userInfoResult[0],
        claimsToday: Number(userInfoResult[1]),
        lastClaimDate: Number(userInfoResult[2]),
        lastClaimTime: Number(userInfoResult[3]),
        pendingEthRewards: ethers.formatEther(userInfoResult[4]),
        pendingTokenRewards: ethers.formatEther(userInfoResult[5]),
        totalEthEarned: ethers.formatEther(userInfoResult[6]),
        totalTokensEarned: ethers.formatEther(userInfoResult[7]),
        referralCount: Number(userInfoResult[8])
      });
      
      setClaimStatus({
        canClaim: claimStatusResult[0],
        message: claimStatusResult[1],
        timeLeft: calculateTimeLeft(userInfoResult[3])
      });
    } catch (error) {
      console.error(':', error);
      
      // 3
      if ((error.message.includes('missing revert data') || 
           error.message.includes('ERR_CONNECTION_CLOSED') ||
           error.code === 'CALL_EXCEPTION') && retryCount < 3) {
        console.log(`... (${retryCount + 1}/3)`);
        setTimeout(() => {
          fetchUserInfo(retryCount + 1);
        }, 2000); // 2
      }
    } finally {
      setLoading(prev => ({ ...prev, fetchingData: false }));
    }
  };

  // 
  const calculateTimeLeft = (lastClaimTime) => {
    if (!lastClaimTime || lastClaimTime === 0) return 0;
    
    const now = Math.floor(Date.now() / 1000);
    const claimInterval = 8 * 60 * 60; // 8
    const nextClaimTime = Number(lastClaimTime) + claimInterval;
    
    return Math.max(0, nextClaimTime - now);
  };

  // 
  const bindReferrer = async (referrerAddress) => {
    if (!contract) return { success: false, error: '' };
    
    try {
      setLoading(prev => ({ ...prev, bindReferrer: true }));
      
      // 
      if (!ethers.isAddress(referrerAddress)) {
        return { success: false, error: '' };
      }
      
      if (referrerAddress.toLowerCase() === account.toLowerCase()) {
        return { success: false, error: '' };
      }
      
      // ETH
      const tx = await contract.bindReferrer(referrerAddress);
      await tx.wait();
      
      // 
      await fetchUserInfo();
      
      return { success: true, hash: tx.hash };
    } catch (error) {
      console.error(':', error);
      return { 
        success: false, 
        error: error.reason || error.message || ''
      };
    } finally {
      setLoading(prev => ({ ...prev, bindReferrer: false }));
    }
  };

  // 
  const generateRandomPayment = () => {
    const min = 0.0002;
    const max = 0.0006;
    // 
    const random = Math.random() * (max - min) + min;
    // 
    const safeValue = Math.min(random, 0.000599);
    return safeValue.toFixed(6); // 6
  };

  // MT
  const claimTokens = async () => {
    if (!contract) return { success: false, error: '' };
    
    // 
    if (!claimStatus.canClaim) {
      return { success: false, error: claimStatus.message };
    }
    
    try {
      setLoading(prev => ({ ...prev, claimTokens: true }));
      
      // 
      const randomPayment = generateRandomPayment();
      const value = ethers.parseEther(randomPayment);
      
      // 
      const tx = await contract.claimTokens({ value });
      await tx.wait();
      
      // 
      alert(`MT\n: ${tx.hash}\n\n...`);
      
      // 
      setTimeout(async () => {
        await fetchUserInfo();
      }, 3000);
      
      return { success: true, hash: tx.hash };
    } catch (error) {
      console.error(':', error);
      let errorMessage = '';
      
      if (error.message.includes('Claim interval not met')) {
        errorMessage = '8';
      } else if (error.message.includes('Max claims reached')) {
        errorMessage = '';
      } else if (error.message.includes('Payment amount out of range')) {
        errorMessage = '';
      } else if (error.message.includes('Network Error') || error.code === 'UNKNOWN_ERROR') {
        errorMessage = 'RPC';
      } else if (error.code === -32603) {
        errorMessage = 'RPCArbitrum RPC';
      }
      
      return { 
        success: false, 
        error: error.reason || errorMessage
      };
    } finally {
      setLoading(prev => ({ ...prev, claimTokens: false }));
    }
  };

  // ETH
  const claimEthRewards = async () => {
    if (!contract) return { success: false, error: '' };
    
    try {
      setLoading(prev => ({ ...prev, claimEthRewards: true }));
      
      const tx = await contract.claimEthRewards();
      await tx.wait();
      
      // 
      alert(`ETH\n: ${tx.hash}\n\n...`);
      
      // 
      setTimeout(async () => {
        await fetchUserInfo();
      }, 3000);
      
      return { success: true, hash: tx.hash };
    } catch (error) {
      console.error('ETH:', error);
      return { 
        success: false, 
        error: error.reason || error.message || ''
      };
    } finally {
      setLoading(prev => ({ ...prev, claimEthRewards: false }));
    }
  };

  // 
  const claimTokenRewards = async () => {
    if (!contract) return { success: false, error: '' };
    
    try {
      setLoading(prev => ({ ...prev, claimTokenRewards: true }));
      
      const tx = await contract.claimTokenRewards();
      await tx.wait();
      
      // 
      alert(`MT\n: ${tx.hash}\n\n...`);
      
      // 
      setTimeout(async () => {
        await fetchUserInfo();
      }, 3000);
      
      return { success: true, hash: tx.hash };
    } catch (error) {
      console.error(':', error);
      return { 
        success: false, 
        error: error.reason || error.message || ''
      };
    } finally {
      setLoading(prev => ({ ...prev, claimTokenRewards: false }));
    }
  };

  // 
  const formatAddress = (address) => {
    if (!address || address === ethers.ZeroAddress) return '';
    return `${address.slice(0, 6)}...${address.slice(-4)}`;
  };

  // 
  const canClaim = () => {
    return claimStatus.canClaim && userInfo.claimsToday < 2;
  };

  // 
  const formatTimeLeft = (seconds) => {
    if (seconds <= 0) return '';
    
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const secs = seconds % 60;
    
    if (hours > 0) {
      return `${hours}${minutes}`;
    } else if (minutes > 0) {
      return `${minutes}${secs}`;
    } else {
      return `${secs}`;
    }
  };

  useEffect(() => {
    fetchUserInfo();
  }, [contract, account]);

  return {
    userInfo,
    claimStatus,
    loading,
    claimTokens,
    claimEthRewards,
    claimTokenRewards,
    bindReferrer,
    fetchUserInfo,
    formatAddress,
    formatTimeLeft,
    canClaim
  };
};